import React, { useState } from "react";
import Layout from "./layout";
import { Sparkles } from "lucide-react";

const Tool001 = () => {
  const [subject, setSubject] = useState("");
  const [topic, setTopic] = useState("");
  const [yearGroup, setYearGroup] = useState("");
  const [response, setResponse] = useState("");
  const [loading, setLoading] = useState(false);

  const agentId = "asst_1UapHOvlRnY8SYKyizeXmUcZ";

  const handleSubmit = async () => {
    if (!subject || !topic || !yearGroup) {
      setResponse("Please fill in all fields.");
      return;
    }

    const message = `Please help me plan a lesson.\nSubject: ${subject}\nTopic: ${topic}\nYear Group: ${yearGroup}\nProvide a detailed lesson plan.`;

    setLoading(true);
    setResponse("Thinking...");

    try {
      const res = await fetch("/ask", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ agentId, message }),
      });
      const data = await res.json();
      setResponse(data.response || data.error || "No response.");
    } catch (err) {
      setResponse("Error: " + err.message);
    }

    setLoading(false);
  };

  return (
    <Layout>
      <div className="p-6 max-w-4xl mx-auto space-y-6">
        {/* Header */}
        <div className="space-y-2">
          <h1 className="text-2xl font-bold flex items-center gap-2">
            <Sparkles className="w-5 h-5 text-green-700" />
            Lesson Planning Tool
          </h1>
          <p className="text-gray-600">Generate a full lesson plan by entering the subject, topic, and year group.</p>
        </div>

        {/* Form Card */}
        <div className="bg-white border rounded-xl p-6 shadow-md space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Subject</label>
            <input
              type="text"
              value={subject}
              onChange={(e) => setSubject(e.target.value)}
              placeholder="e.g. History"
              className="w-full border border-gray-300 rounded-md px-4 py-2"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Topic</label>
            <input
              type="text"
              value={topic}
              onChange={(e) => setTopic(e.target.value)}
              placeholder="e.g. The Industrial Revolution"
              className="w-full border border-gray-300 rounded-md px-4 py-2"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Year Group</label>
            <select
              value={yearGroup}
              onChange={(e) => setYearGroup(e.target.value)}
              className="w-full border border-gray-300 rounded-md px-4 py-2"
            >
              <option value="">Select a year group</option>
              <option value="Year 7">Year 7</option>
              <option value="Year 8">Year 8</option>
              <option value="Year 9">Year 9</option>
              <option value="Year 10">Year 10</option>
              <option value="Year 11">Year 11</option>
            </select>
          </div>

          <button
            onClick={handleSubmit}
            disabled={loading}
            className="bg-green-700 text-white px-6 py-2 rounded-md hover:bg-green-800 transition"
          >
            {loading ? "Generating..." : "Generate Lesson Plan"}
          </button>
        </div>

        {/* Output Card */}
        <div className="bg-gray-50 border rounded-xl p-6 shadow-inner whitespace-pre-wrap text-sm text-gray-800">
          {response}
        </div>
      </div>
    </Layout>
  );
};

export default Tool001;
