import React, { useState } from "react";
import { Search, Star, Flame, Sparkles } from "lucide-react";
import Layout from "./layout";
import { useNavigate } from "react-router-dom";

const tools = [
  {
    name: "Lesson Planning",
    description: "Write professional, individualised lessons for your whole class",
    tag: "New",
    category: "Planning",
    locked: false,
    href: "/tool001",
  },
  {
    name: "Presentation Maker",
    description: "Design captivating presentations on any topic with ease",
    tag: "New",
    category: "Admin",
    locked: false,
  },
  {
    name: "AI Resistant Tasks",
    description: "Premium-only tool for generating robust assessment tasks",
    tag: "New",
    category: "Assessment",
    locked: false,
  },
  {
    name: "Exam Question Generator",
    description: "Premium-only tool for custom exam questions",
    tag: "New",
    category: "Assessment",
    locked: false,
  },
  {
    name: "Feedback Generator",
    description: "Generate detailed, personalised feedback",
    tag: "New",
    category: "Assessment",
    locked: false,
  },
  {
    name: "Image Generator",
    description: "Create custom AI images from text prompts",
    tag: "New",
    category: "Admin",
    locked: false,
  },
  {
    name: "Increasing Complexity",
    description: "Scaffold tasks across ability levels",
    tag: "New",
    category: "Planning",
    locked: false,
  },
  {
    name: "Strengths and Weaknesses",
    description: "Highlights strengths and areas for improvement",
    tag: "New",
    category: "SEN",
    locked: false,
  },
];

export default function Homepage() {
  const navigate = useNavigate();
  const [favourites, setFavourites] = useState(() => {
    const saved = localStorage.getItem("favourites");
    return saved ? JSON.parse(saved) : [];
  });
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("All");
  const [clickedStar, setClickedStar] = useState(null);

  const categories = [
    "All",
    "Favourites",
    "Assessment",
    "Planning",
    "SEN",
    "Leadership",
    "Admin",
  ];

  const handleCardClick = (tool) => {
    if (tool.href) {
      navigate(tool.href);
    }
  };

  const toggleFavourite = (toolName) => {
    setFavourites((prev) => {
      const updated = prev.includes(toolName)
        ? prev.filter((name) => name !== toolName)
        : [...prev, toolName];
      localStorage.setItem("favourites", JSON.stringify(updated));
      return updated;
    });
    setClickedStar(toolName);
    setTimeout(() => setClickedStar(null), 400); // Reset animation
  };

  const filteredTools = tools
    .filter((tool) => {
      const matchesSearch =
        tool.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        tool.description.toLowerCase().includes(searchTerm.toLowerCase());

      const matchesCategory =
        selectedCategory === "All" ||
        (selectedCategory === "Favourites" && favourites.includes(tool.name)) ||
        tool.category === selectedCategory;

      return matchesSearch && matchesCategory;
    })
    .sort((a, b) => {
      if (selectedCategory === "Favourites") return 0; // keep original order
      const aFav = favourites.includes(a.name) ? 1 : 0;
      const bFav = favourites.includes(b.name) ? 1 : 0;
      return bFav - aFav; // show favourites first
    });

  return (
    <Layout>
      <div className="p-6 space-y-6 overflow-y-auto h-full">
        {/* Top bar with search */}
        <div className="relative w-full">
          <input
            type="text"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            placeholder="Search tools, templates, or keywords..."
            className="w-full border border-gray-300 rounded-md px-4 py-2 pr-10"
          />
          <Search className="absolute right-3 top-2.5 h-5 w-5 text-gray-400" />
        </div>

        {/* Filter badges */}
        <div className="flex flex-wrap gap-4 items-center justify-between">
          {categories.map((tag) => (
            <span
              key={tag}
              onClick={() => setSelectedCategory(tag)}
              className={`flex-1 min-w-[120px] text-center px-4 py-2 border rounded-full text-sm cursor-pointer transition-all ${
                selectedCategory === tag ? "bg-gray-400 text-white" : "bg-gray-200 hover:bg-gray-300"
              }`}
            >
              {tag}
            </span>
          ))}
        </div>

        {/* Tool cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredTools.map((tool, idx) => (
            <div
              key={idx}
              onClick={() => handleCardClick(tool)}
              className="relative border rounded-xl p-4 shadow-md bg-white transform transition duration-300 hover:scale-102 hover:shadow-lg cursor-pointer h-48 flex flex-col justify-between"
            >
              {/* Favourite button */}
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  toggleFavourite(tool.name);
                }}
                className="absolute top-3 right-3 p-2 rounded-full hover:bg-yellow-100 transition"
              >
                <Star
                  className={`w-5 h-5 transition-transform duration-300 ${
                    favourites.includes(tool.name) ? "text-yellow-400" : "text-gray-400"
                  } ${clickedStar === tool.name ? "scale-125 animate-ping-once" : ""}`}
                />
              </button>

              {/* Tool content */}
              <div>
                <h3 className="text-lg font-semibold pr-8">{tool.name}</h3>
                <p className="text-sm text-gray-600 mt-1">{tool.description}</p>
              </div>

              {/* Tags */}
              <div className="absolute bottom-4 left-4 flex flex-wrap gap-2 text-sm text-gray-500 items-center">
                <span className="bg-green-100 text-green-800 px-2 py-1 rounded-full text-xs font-medium">
                  {tool.category}
                </span>
                {tool.tag === "Hot" && (
                  <span className="bg-red-100 text-red-700 px-2 py-1 rounded-full text-xs font-medium flex items-center gap-1">
                    <Flame className="w-3 h-3" />
                    Hot
                  </span>
                )}
                {tool.tag === "New" && (
                  <span className="bg-blue-100 text-blue-700 px-2 py-1 rounded-full text-xs font-medium flex items-center gap-1">
                    <Sparkles className="w-3 h-3" />
                    New
                  </span>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </Layout>
  );
}